// UI Controller Module
// Handles all DOM interactions and visual updates

export class UIController {
  constructor() {
    this.initializeElements();
    this.feedbackTimeout = null;
  }

  initializeElements() {
    // Use globalThis to work in both browser and Node.js test environments
    const doc = (typeof document !== 'undefined') ? document : globalThis.document;

    if (!doc) {
      console.warn('Document not available for UIController');
      return;
    }

    // Question display
    this.questionText = doc.getElementById('questionText');
    this.questionDisplay = doc.getElementById('questionDisplay');

    // Answer input
    this.answerInput = doc.getElementById('answerInput');
    this.feedback = doc.getElementById('feedback');

    // Statistics
    this.questionsCount = doc.getElementById('questionsCount');
    this.accuracyRate = doc.getElementById('accuracyRate');
    this.avgTime = doc.getElementById('avgTime');
    this.currentStreak = doc.getElementById('currentStreak');

    // Controls
    this.startBtn = doc.getElementById('startBtn');
    this.resetBtn = doc.getElementById('resetBtn');

    // Verify all elements exist
    const requiredElements = [
      'questionText', 'answerInput', 'feedback',
      'questionsCount', 'accuracyRate', 'avgTime', 'currentStreak',
      'startBtn', 'resetBtn'
    ];

    const missingElements = requiredElements.filter(id => !doc.getElementById(id));
    if (missingElements.length > 0) {
      console.error('Missing required elements:', missingElements);
    }
  }

  // Display a new question
  displayQuestion(questionObj) {
    if (!questionObj || !questionObj.question) {
      console.error('Invalid question object:', questionObj);
      return;
    }

    this.questionText.textContent = questionObj.question;
    this.answerInput.value = '';
    this.answerInput.disabled = false;
    this.answerInput.focus();
    // Don't clear feedback immediately - let it fade naturally
    // this.clearFeedback();
  }

  // Show feedback for answer
  showFeedback(isCorrect, correctAnswer = null) {
    // Clear any existing timeout
    if (this.feedbackTimeout) {
      clearTimeout(this.feedbackTimeout);
    }

    // Remove all feedback classes first
    this.feedback.className = 'feedback';

    if (isCorrect) {
      this.feedback.textContent = 'âœ“ Correct!';
      this.feedback.classList.add('feedback-correct');

      // Pulse animation on question display
      this.questionDisplay.classList.add('pulse');
      setTimeout(() => {
        this.questionDisplay.classList.remove('pulse');
      }, 600);
    } else {
      this.feedback.textContent = `âœ— Incorrect. Answer: ${correctAnswer}`;
      this.feedback.classList.add('feedback-incorrect');

      // Shake animation on answer input
      this.answerInput.classList.add('shake');
      setTimeout(() => {
        this.answerInput.classList.remove('shake');
      }, 300);
    }

    // Auto-clear feedback after delay (stays visible while answering next question)
    this.feedbackTimeout = setTimeout(() => {
      this.clearFeedback();
    }, isCorrect ? 4000 : 5000);
  }

  // Clear feedback display
  clearFeedback() {
    this.feedback.textContent = '';
    this.feedback.className = 'feedback';
    if (this.feedbackTimeout) {
      clearTimeout(this.feedbackTimeout);
      this.feedbackTimeout = null;
    }
  }

  // Update statistics display
  updateStatistics(stats) {
    if (!stats) return;

    // Update each stat with animation
    this.animateValue(this.questionsCount, stats.totalQuestions);
    this.animateValue(this.accuracyRate, `${stats.accuracy}%`);
    this.animateValue(this.avgTime, `${stats.averageTime}s`);
    this.animateValue(this.currentStreak, stats.currentStreak);

    // Special effect for streak milestones
    if (stats.currentStreak > 0 && stats.currentStreak % 5 === 0) {
      this.celebrateStreak(stats.currentStreak);
    }
  }

  // Animate value changes
  animateValue(element, newValue) {
    const currentValue = element.textContent;
    if (currentValue !== newValue.toString()) {
      element.style.transform = 'scale(1.2)';
      element.textContent = newValue;
      setTimeout(() => {
        element.style.transform = 'scale(1)';
      }, 200);
    }
  }

  // Celebrate streak milestones
  celebrateStreak(streak) {
    const streakElement = this.currentStreak.parentElement;
    streakElement.classList.add('pulse');

    // Show temporary celebration message
    const originalText = this.feedback.textContent;
    if (!originalText) {
      this.feedback.textContent = `ðŸ”¥ ${streak} streak!`;
      this.feedback.className = 'feedback feedback-correct';
      setTimeout(() => {
        if (this.feedback.textContent === `ðŸ”¥ ${streak} streak!`) {
          this.clearFeedback();
        }
      }, 2000);
    }

    setTimeout(() => {
      streakElement.classList.remove('pulse');
    }, 600);
  }

  // Enable/disable game controls
  setGameActive(isActive) {
    if (isActive) {
      this.startBtn.textContent = 'Next Question';
      this.startBtn.classList.add('btn-active');
      this.answerInput.disabled = false;
      this.resetBtn.disabled = false;
    } else {
      this.startBtn.textContent = 'Start Practice';
      this.startBtn.classList.remove('btn-active');
      this.answerInput.disabled = true;
      this.answerInput.value = '';
      this.questionText.textContent = 'Press Start to begin!';
      this.clearFeedback();
    }
  }

  // Reset UI to initial state
  reset() {
    this.setGameActive(false);
    this.clearFeedback();

    // Reset statistics display
    this.questionsCount.textContent = '0';
    this.accuracyRate.textContent = '--%';
    this.avgTime.textContent = '--s';
    this.currentStreak.textContent = '0';

    // Clear any animations
    const animated = document.querySelectorAll('.pulse, .shake');
    animated.forEach(el => {
      el.classList.remove('pulse', 'shake');
    });
  }

  // Get user's answer
  getAnswer() {
    return this.answerInput ? this.answerInput.value.trim() : '';
  }

  // Clear answer input
  clearAnswer() {
    if (this.answerInput) {
      this.answerInput.value = '';
      this.answerInput.focus();
    }
  }

  // Show loading state
  showLoading() {
    if (this.questionText) {
      this.questionText.classList.add('loading');
      this.questionText.textContent = '';
    }
  }

  // Hide loading state
  hideLoading() {
    if (this.questionText) {
      this.questionText.classList.remove('loading');
    }
  }

  // Display error message
  showError(message) {
    this.feedback.textContent = `âš ï¸ ${message}`;
    this.feedback.className = 'feedback feedback-incorrect';
  }

  // Add event listener helper
  on(element, event, handler) {
    if (element) {
      element.addEventListener(event, handler);
    }
  }

  // Remove event listener helper
  off(element, event, handler) {
    if (element) {
      element.removeEventListener(event, handler);
    }
  }

  // Focus answer input
  focusAnswer() {
    if (this.answerInput) {
      this.answerInput.focus();
      this.answerInput.select();
    }
  }

  // Check if answer input is focused
  isAnswerFocused() {
    return document.activeElement === this.answerInput;
  }

  // Flash an element (for attention)
  flash(element) {
    if (!element) return;

    element.style.transition = 'background-color 0.3s';
    const originalBg = element.style.backgroundColor;
    element.style.backgroundColor = 'var(--primary-light)';

    setTimeout(() => {
      element.style.backgroundColor = originalBg;
    }, 300);
  }

  // Update button states
  updateButtonStates(canStart, canReset) {
    this.startBtn.disabled = !canStart;
    this.resetBtn.disabled = !canReset;
  }

  // Show session summary
  showSessionSummary(stats) {
    const summary = `
      Session Complete!
      Questions: ${stats.totalQuestions}
      Accuracy: ${stats.accuracy}%
      Best Streak: ${stats.bestStreak}
      Avg Time: ${stats.averageTime}s
    `;

    if (this.questionText) {
      this.questionText.textContent = summary.trim();
    }
    this.setGameActive(false);
  }

  // Handle keyboard shortcuts display
  showKeyboardShortcuts() {
    const shortcuts = `
      Keyboard Shortcuts:
      Enter - Submit answer / Next question
      Escape - Reset session
      Space - Start/Next (when input not focused)
    `;

    this.feedback.textContent = shortcuts.trim();
    this.feedback.className = 'feedback';
  }
}

// Export only the class, not a singleton instance
// This prevents issues with DOM access before it's ready (especially in tests)
// main.js will create its own instance when needed