// High Scores Module
// Manages local leaderboards and high score tracking for Sprint Mode

export class HighScores {
  constructor(storageKey = 'mathFluencyHighScores') {
    this.storageKey = storageKey;
    this.maxScoresPerDuration = 10;
    this.loadScores();
  }

  loadScores() {
    try {
      const saved = localStorage.getItem(this.storageKey);
      this.scores = saved ? JSON.parse(saved) : this.getEmptyScores();
      this.validateScores();
    } catch (error) {
      console.error('Failed to load high scores:', error);
      this.scores = this.getEmptyScores();
    }
  }

  getEmptyScores() {
    return {
      '1min': [],
      '2min': [],
      '3min': [],
      '5min': []
    };
  }

  validateScores() {
    const durations = ['1min', '2min', '3min', '5min'];
    
    durations.forEach(duration => {
      if (!Array.isArray(this.scores[duration])) {
        this.scores[duration] = [];
      }
      
      // Ensure each score has required fields
      this.scores[duration] = this.scores[duration].filter(score => 
        score && 
        typeof score.name === 'string' &&
        typeof score.score === 'number' &&
        typeof score.questionsAnswered === 'number' &&
        typeof score.accuracy === 'number' &&
        score.date
      );
      
      // Sort and limit
      this.scores[duration].sort((a, b) => b.score - a.score);
      this.scores[duration] = this.scores[duration].slice(0, this.maxScoresPerDuration);
    });
  }

  saveScores() {
    try {
      localStorage.setItem(this.storageKey, JSON.stringify(this.scores));
      return true;
    } catch (error) {
      console.error('Failed to save high scores:', error);
      return false;
    }
  }

  addScore(duration, scoreData) {
    // Validate duration
    if (!['1min', '2min', '3min', '5min'].includes(duration)) {
      throw new Error(`Invalid duration: ${duration}`);
    }

    // Validate score data
    if (!scoreData.name || typeof scoreData.name !== 'string') {
      throw new Error('Invalid name');
    }
    if (typeof scoreData.score !== 'number' || scoreData.score < 0) {
      throw new Error('Invalid score');
    }
    if (typeof scoreData.questionsAnswered !== 'number' || scoreData.questionsAnswered < 0) {
      throw new Error('Invalid questionsAnswered');
    }
    if (typeof scoreData.accuracy !== 'number' || scoreData.accuracy < 0 || scoreData.accuracy > 100) {
      throw new Error('Invalid accuracy');
    }

    // Create score entry
    const entry = {
      name: scoreData.name.trim(),
      score: Math.round(scoreData.score),
      questionsAnswered: scoreData.questionsAnswered,
      accuracy: Math.round(scoreData.accuracy * 10) / 10, // Round to 1 decimal
      date: new Date().toISOString(),
      avgTimePerQuestion: scoreData.avgTimePerQuestion || 0
    };

    // Add to scores
    this.scores[duration].push(entry);
    
    // Sort by score (descending)
    this.scores[duration].sort((a, b) => b.score - a.score);
    
    // Keep only top scores
    this.scores[duration] = this.scores[duration].slice(0, this.maxScoresPerDuration);
    
    // Save to localStorage
    this.saveScores();
    
    // Return rank (1-based) or null if didn't make the board
    const rank = this.scores[duration].findIndex(s => 
      s.name === entry.name && 
      s.score === entry.score && 
      s.date === entry.date
    );
    
    return rank >= 0 ? rank + 1 : null;
  }

  getScores(duration) {
    if (!['1min', '2min', '3min', '5min'].includes(duration)) {
      throw new Error(`Invalid duration: ${duration}`);
    }
    // Return deep copy to prevent modification
    return this.scores[duration].map(score => ({ ...score }));
  }

  getAllScores() {
    return {
      '1min': this.scores['1min'].map(s => ({ ...s })),
      '2min': this.scores['2min'].map(s => ({ ...s })),
      '3min': this.scores['3min'].map(s => ({ ...s })),
      '5min': this.scores['5min'].map(s => ({ ...s }))
    };
  }

  isHighScore(duration, score) {
    if (!['1min', '2min', '3min', '5min'].includes(duration)) {
      return false;
    }
    
    const scores = this.scores[duration];
    
    // If less than max scores, it's a high score
    if (scores.length < this.maxScoresPerDuration) {
      return true;
    }
    
    // Check if score beats the lowest score
    const lowestScore = scores[scores.length - 1]?.score || 0;
    return score > lowestScore;
  }

  clearScores(duration = null) {
    if (duration) {
      if (!['1min', '2min', '3min', '5min'].includes(duration)) {
        throw new Error(`Invalid duration: ${duration}`);
      }
      this.scores[duration] = [];
    } else {
      this.scores = this.getEmptyScores();
    }
    this.saveScores();
  }

  getTopScore(duration) {
    if (!['1min', '2min', '3min', '5min'].includes(duration)) {
      return null;
    }
    return this.scores[duration][0] || null;
  }

  getPlayerBest(playerName, duration = null) {
    if (!playerName) return null;
    
    const name = playerName.trim().toLowerCase();
    
    if (duration) {
      if (!['1min', '2min', '3min', '5min'].includes(duration)) {
        return null;
      }
      return this.scores[duration].find(s => 
        s.name.toLowerCase() === name
      ) || null;
    }
    
    // Get best across all durations
    let best = null;
    ['1min', '2min', '3min', '5min'].forEach(dur => {
      const score = this.scores[dur].find(s => 
        s.name.toLowerCase() === name
      );
      if (score && (!best || score.score > best.score)) {
        best = { ...score, duration: dur };
      }
    });
    
    return best;
  }

  exportData() {
    return {
      version: '1.0',
      exportDate: new Date().toISOString(),
      scores: this.getAllScores()
    };
  }

  importData(data) {
    if (!data || !data.scores) {
      throw new Error('Invalid import data');
    }
    
    this.scores = data.scores;
    this.validateScores();
    this.saveScores();
  }

  getStatistics() {
    const stats = {};
    
    ['1min', '2min', '3min', '5min'].forEach(duration => {
      const scores = this.scores[duration];
      if (scores.length === 0) {
        stats[duration] = {
          totalGames: 0,
          topScore: 0,
          avgScore: 0,
          avgAccuracy: 0
        };
      } else {
        stats[duration] = {
          totalGames: scores.length,
          topScore: scores[0].score,
          avgScore: Math.round(scores.reduce((sum, s) => sum + s.score, 0) / scores.length),
          avgAccuracy: Math.round(scores.reduce((sum, s) => sum + s.accuracy, 0) / scores.length * 10) / 10
        };
      }
    });
    
    return stats;
  }
}

// Create singleton instance
export const highScores = new HighScores();