// Game Session State Management
// Tracks timing, answers, accuracy, and statistics

export class GameSession {
  constructor() {
    this.reset();
  }

  reset() {
    this.questions = [];        // Array of question records
    this.currentQuestion = null;
    this.questionStartTime = null;
    this.sessionStartTime = null;
    this.currentStreak = 0;
    this.bestStreak = 0;
    this.isActive = false;
    this.isPausedState = false;
  }

  // Start the session
  start() {
    this.reset();
    this.isActive = true;
    this.sessionStartTime = Date.now();
  }

  // Start timing a new question
  startQuestion(questionObj) {
    if (!this.isActive) {
      this.start();
    }

    this.currentQuestion = {
      ...questionObj,
      startTime: Date.now(),
      userAnswer: null,
      isCorrect: null,
      responseTime: null
    };
    this.questionStartTime = Date.now();
  }

  // Submit an answer for the current question
  submitAnswer(userAnswer) {
    if (!this.currentQuestion) {
      throw new Error('No question is currently active');
    }

    const responseTime = Date.now() - this.questionStartTime;
    const numericAnswer = parseFloat(userAnswer);

    // Check if answer is correct (with small tolerance for floating point)
    const isCorrect = Math.abs(numericAnswer - this.currentQuestion.answer) < 0.01;

    // Update current question record
    this.currentQuestion.userAnswer = numericAnswer;
    this.currentQuestion.isCorrect = isCorrect;
    this.currentQuestion.responseTime = responseTime;

    // Add to questions history
    this.questions.push({
      ...this.currentQuestion
    });

    // Update streak
    if (isCorrect) {
      this.currentStreak++;
      this.bestStreak = Math.max(this.bestStreak, this.currentStreak);
    } else {
      this.currentStreak = 0;
    }

    // Clear current question
    const result = {
      isCorrect,
      correctAnswer: this.currentQuestion.answer,
      responseTime
    };

    this.currentQuestion = null;
    this.questionStartTime = null;

    return result;
  }

  // Get current statistics
  getStatistics() {
    const totalQuestions = this.questions.length;

    if (totalQuestions === 0) {
      return {
        totalQuestions: 0,
        correctAnswers: 0,
        accuracy: 0,
        averageTime: 0,
        currentStreak: 0,
        bestStreak: 0,
        sessionDuration: 0,
        questionsByType: {},
        accuracyByType: {}
      };
    }

    const correctAnswers = this.questions.filter(q => q.isCorrect).length;
    const accuracy = (correctAnswers / totalQuestions) * 100;

    // Calculate average response time (only for answered questions)
    const totalTime = this.questions.reduce((sum, q) => sum + q.responseTime, 0);
    const averageTime = totalTime / totalQuestions;

    // Calculate statistics by question type
    const questionsByType = {};
    const accuracyByType = {};

    this.questions.forEach(q => {
      if (!questionsByType[q.type]) {
        questionsByType[q.type] = 0;
        accuracyByType[q.type] = { correct: 0, total: 0 };
      }
      questionsByType[q.type]++;
      accuracyByType[q.type].total++;
      if (q.isCorrect) {
        accuracyByType[q.type].correct++;
      }
    });

    // Convert accuracy by type to percentages
    Object.keys(accuracyByType).forEach(type => {
      const { correct, total } = accuracyByType[type];
      accuracyByType[type] = total > 0 ? (correct / total) * 100 : 0;
    });

    const sessionDuration = this.sessionStartTime
      ? Date.now() - this.sessionStartTime
      : 0;

    return {
      totalQuestions,
      correctAnswers,
      accuracy: Math.round(accuracy * 10) / 10, // Round to 1 decimal
      averageTime: Math.round(averageTime / 100) / 10, // Convert to seconds, 1 decimal
      currentStreak: this.currentStreak,
      bestStreak: this.bestStreak,
      sessionDuration,
      questionsByType,
      accuracyByType
    };
  }

  // Get detailed history
  getHistory() {
    return [...this.questions];
  }

  // Get the last N questions
  getRecentQuestions(n = 5) {
    return this.questions.slice(-n);
  }

  // Check if a session is currently active
  isSessionActive() {
    return this.isActive;
  }

  // Pause the session
  pause() {
    if (this.isActive) {
      this.isPausedState = true;
    }
  }

  // Resume the session
  resume() {
    if (this.isActive && this.isPausedState) {
      this.isPausedState = false;
    }
  }

  // Check if paused
  isPaused() {
    return this.isPausedState;
  }

  // End the session
  end() {
    this.isActive = false;
    this.isPausedState = false;
    return this.getStatistics();
  }

  // Get performance trends (for last N questions)
  getPerformanceTrend(windowSize = 10) {
    if (this.questions.length < windowSize) {
      return null;
    }

    const recentQuestions = this.questions.slice(-windowSize);
    const recentAccuracy = (recentQuestions.filter(q => q.isCorrect).length / windowSize) * 100;

    const olderQuestions = this.questions.slice(-windowSize * 2, -windowSize);
    if (olderQuestions.length < windowSize) {
      return { trend: 'stable', recentAccuracy };
    }

    const olderAccuracy = (olderQuestions.filter(q => q.isCorrect).length / windowSize) * 100;

    let trend = 'stable';
    if (recentAccuracy > olderAccuracy + 10) {
      trend = 'improving';
    } else if (recentAccuracy < olderAccuracy - 10) {
      trend = 'declining';
    }

    return {
      trend,
      recentAccuracy: Math.round(recentAccuracy),
      olderAccuracy: Math.round(olderAccuracy)
    };
  }

  // Export session data (for potential save/load feature)
  exportData() {
    return {
      questions: this.questions,
      currentStreak: this.currentStreak,
      bestStreak: this.bestStreak,
      sessionStartTime: this.sessionStartTime,
      isActive: this.isActive
    };
  }

  // Import session data
  importData(data) {
    this.questions = data.questions || [];
    this.currentStreak = data.currentStreak || 0;
    this.bestStreak = data.bestStreak || 0;
    this.sessionStartTime = data.sessionStartTime || null;
    this.isActive = data.isActive || false;
    this.currentQuestion = null;
    this.questionStartTime = null;
  }
}

// Create and export a singleton instance
export const gameSession = new GameSession();