// Sprint Mode Module
// Manages timed practice sessions with scoring and performance tracking

import { getRandomQuestion } from './questionGenerator.js';
import { highScores } from './highScores.js';

export const SPRINT_DURATIONS = {
  '1min': 60000,
  '2min': 120000,
  '3min': 180000,
  '5min': 300000
};

export const SPRINT_STATES = {
  IDLE: 'idle',
  READY: 'ready',
  RUNNING: 'running',
  PAUSED: 'paused',
  FINISHED: 'finished'
};

export class SprintMode {
  constructor() {
    this.reset();
  }

  reset() {
    this.state = SPRINT_STATES.IDLE;
    this.duration = null;
    this.startTime = null;
    this.endTime = null;
    this.pausedTime = 0;
    this.pauseStartTime = null;
    this.timeRemaining = 0;
    
    this.questions = [];
    this.currentQuestion = null;
    this.questionStartTime = null;
    
    this.score = 0;
    this.correctAnswers = 0;
    this.totalAnswers = 0;
    this.currentStreak = 0;
    this.bestStreak = 0;
    
    this.updateCallback = null;
    this.finishCallback = null;
    this.updateInterval = null;
  }

  configure(duration, options = {}) {
    if (!SPRINT_DURATIONS[duration]) {
      throw new Error(`Invalid duration: ${duration}`);
    }
    
    if (this.state !== SPRINT_STATES.IDLE) {
      throw new Error('Cannot configure sprint while active');
    }
    
    this.duration = duration;
    this.timeRemaining = SPRINT_DURATIONS[duration];
    this.updateCallback = options.onUpdate || null;
    this.finishCallback = options.onFinish || null;
    this.state = SPRINT_STATES.READY;
    
    return true;
  }

  start() {
    if (this.state !== SPRINT_STATES.READY) {
      throw new Error('Sprint not configured or already running');
    }
    
    this.state = SPRINT_STATES.RUNNING;
    this.startTime = Date.now();
    this.nextQuestion();
    
    // Start timer updates
    this.updateInterval = setInterval(() => {
      this.updateTimer();
    }, 100); // Update every 100ms for smooth countdown
    
    return true;
  }

  pause() {
    if (this.state !== SPRINT_STATES.RUNNING) {
      return false;
    }
    
    this.state = SPRINT_STATES.PAUSED;
    this.pauseStartTime = Date.now();
    
    if (this.updateInterval) {
      clearInterval(this.updateInterval);
      this.updateInterval = null;
    }
    
    return true;
  }

  resume() {
    if (this.state !== SPRINT_STATES.PAUSED) {
      return false;
    }
    
    // Add paused duration to total paused time
    this.pausedTime += Date.now() - this.pauseStartTime;
    this.pauseStartTime = null;
    this.state = SPRINT_STATES.RUNNING;
    
    // Restart timer updates
    this.updateInterval = setInterval(() => {
      this.updateTimer();
    }, 100);
    
    return true;
  }

  stop() {
    if (this.state === SPRINT_STATES.IDLE) {
      return false;
    }

    // Clean up
    if (this.updateInterval) {
      clearInterval(this.updateInterval);
      this.updateInterval = null;
    }

    // Calculate final time if was running
    const wasRunning = this.state === SPRINT_STATES.RUNNING;
    if (wasRunning) {
      this.endTime = Date.now();
    }

    this.state = SPRINT_STATES.FINISHED;

    // Trigger finish callback if sprint was active
    if (wasRunning && this.finishCallback) {
      this.finishCallback(this.getResults());
    }

    return true;
  }

  nextQuestion() {
    if (this.state !== SPRINT_STATES.RUNNING) {
      return null;
    }
    
    this.currentQuestion = getRandomQuestion();
    this.questionStartTime = Date.now();
    
    return this.currentQuestion;
  }

  submitAnswer(userAnswer) {
    if (this.state !== SPRINT_STATES.RUNNING || !this.currentQuestion) {
      return null;
    }
    
    const timeElapsed = Date.now() - this.questionStartTime;
    const isCorrect = parseFloat(userAnswer) === this.currentQuestion.answer;
    
    // Record the question
    const questionRecord = {
      question: this.currentQuestion.question,
      userAnswer: userAnswer,
      correctAnswer: this.currentQuestion.answer,
      isCorrect: isCorrect,
      timeElapsed: timeElapsed,
      type: this.currentQuestion.type
    };
    this.questions.push(questionRecord);
    
    // Update statistics
    this.totalAnswers++;

    if (isCorrect) {
      this.correctAnswers++;
      this.currentStreak++;
      this.bestStreak = Math.max(this.bestStreak, this.currentStreak);
      this.score += 1; // +1 for correct
    } else {
      this.currentStreak = 0;
      this.score = Math.max(0, this.score - 1); // -1 for incorrect, but don't go below 0
    }

    return {
      isCorrect: isCorrect,
      correctAnswer: this.currentQuestion.answer,
      points: isCorrect ? 1 : -1,
      currentStreak: this.currentStreak,
      score: this.score
    };
  }

  updateTimer() {
    if (this.state !== SPRINT_STATES.RUNNING) {
      return;
    }
    
    const now = Date.now();
    const elapsed = now - this.startTime - this.pausedTime;
    this.timeRemaining = Math.max(0, SPRINT_DURATIONS[this.duration] - elapsed);
    
    // Trigger update callback
    if (this.updateCallback) {
      this.updateCallback({
        timeRemaining: this.timeRemaining,
        score: this.score,
        questionsAnswered: this.totalAnswers
      });
    }
    
    // Check if time is up
    if (this.timeRemaining <= 0) {
      this.stop();
    }
  }

  getTimeRemaining() {
    return this.timeRemaining;
  }

  getFormattedTime() {
    const totalSeconds = Math.ceil(this.timeRemaining / 1000);
    const minutes = Math.floor(totalSeconds / 60);
    const seconds = totalSeconds % 60;
    return `${minutes}:${seconds.toString().padStart(2, '0')}`;
  }

  getProgress() {
    if (!this.duration) return 0;
    const totalTime = SPRINT_DURATIONS[this.duration];
    return ((totalTime - this.timeRemaining) / totalTime) * 100;
  }

  getStatistics() {
    const accuracy = this.totalAnswers > 0 
      ? Math.round((this.correctAnswers / this.totalAnswers) * 100)
      : 0;
    
    const avgTimePerQuestion = this.questions.length > 0
      ? Math.round(this.questions.reduce((sum, q) => sum + q.timeElapsed, 0) / this.questions.length)
      : 0;
    
    return {
      score: this.score,
      questionsAnswered: this.totalAnswers,
      correctAnswers: this.correctAnswers,
      accuracy: accuracy,
      currentStreak: this.currentStreak,
      bestStreak: this.bestStreak,
      avgTimePerQuestion: avgTimePerQuestion,
      timeElapsed: this.startTime ? 
        (this.endTime || Date.now()) - this.startTime - this.pausedTime : 0
    };
  }

  getResults() {
    if (this.state === SPRINT_STATES.IDLE) {
      return null;
    }
    
    const stats = this.getStatistics();
    
    return {
      duration: this.duration,
      score: stats.score,
      questionsAnswered: stats.questionsAnswered,
      correctAnswers: stats.correctAnswers,
      accuracy: stats.accuracy,
      bestStreak: stats.bestStreak,
      avgTimePerQuestion: stats.avgTimePerQuestion,
      timeElapsed: stats.timeElapsed,
      questions: [...this.questions],
      timestamp: new Date().toISOString()
    };
  }

  isHighScore() {
    if (!this.duration || this.state === SPRINT_STATES.IDLE) {
      return false;
    }
    return highScores.isHighScore(this.duration, this.score);
  }

  saveHighScore(playerName) {
    if (!this.duration || !playerName || this.state === SPRINT_STATES.IDLE) {
      return null;
    }
    
    const stats = this.getStatistics();
    return highScores.addScore(this.duration, {
      name: playerName,
      score: stats.score,
      questionsAnswered: stats.questionsAnswered,
      accuracy: stats.accuracy,
      avgTimePerQuestion: stats.avgTimePerQuestion
    });
  }

  getLeaderboard() {
    if (!this.duration) {
      return [];
    }
    return highScores.getScores(this.duration);
  }

  getState() {
    return this.state;
  }

  isActive() {
    return this.state === SPRINT_STATES.RUNNING || this.state === SPRINT_STATES.PAUSED;
  }

  canStart() {
    return this.state === SPRINT_STATES.READY;
  }

  exportData() {
    return {
      state: this.state,
      duration: this.duration,
      statistics: this.getStatistics(),
      results: this.getResults()
    };
  }
}

// Create singleton instance
export const sprintMode = new SprintMode();