// Main App Orchestration
// Ties together all modules to create the complete game

import { getRandomQuestion } from './questionGenerator.js';
import { gameSession } from './gameSession.js';
import { UIController } from './ui.js';
import { SprintController } from './sprintController.js';

class MathFluencyGame {
  constructor() {
    this.ui = new UIController();
    this.session = gameSession;
    this.sprint = new SprintController(this.ui);
    this.currentQuestion = null;
    this.isWaitingForNext = false;
    this.mode = 'practice'; // 'practice' or 'sprint'

    this.initializeEventListeners();
  }

  initializeEventListeners() {
    // Start/Next button
    this.ui.on(this.ui.startBtn, 'click', () => this.handleStartNext());

    // Reset button
    this.ui.on(this.ui.resetBtn, 'click', () => this.handleReset());

    // Answer submission - use keydown instead of keypress for Enter key detection
    // Note: keypress doesn't fire for Enter in number inputs in modern browsers
    this.ui.on(this.ui.answerInput, 'keydown', (e) => {
      if (e.key === 'Enter') {
        e.preventDefault(); // Prevent form submission
        this.handleAnswerSubmit();
      }
    });

    // Keyboard shortcuts
    document.addEventListener('keydown', (e) => {
      // Escape to reset
      if (e.key === 'Escape') {
        this.handleReset();
      }

      // Space for next question (when input not focused)
      if (e.key === ' ' && !this.ui.isAnswerFocused()) {
        e.preventDefault();
        this.handleStartNext();
      }

      // Show help with ?
      if (e.key === '?' && !this.ui.isAnswerFocused()) {
        this.ui.showKeyboardShortcuts();
      }
    });

    // Note: Enter prevention is already handled in the answer submission handler above

    // Sprint Mode event listeners
    this.initializeSprintListeners();
  }

  initializeSprintListeners() {
    // Sprint button
    const sprintBtn = document.getElementById('sprintBtn');
    if (sprintBtn) {
      sprintBtn.addEventListener('click', () => this.showSprintSelector());
    }

    // Cancel sprint selector
    const cancelBtn = document.getElementById('cancelSprintBtn');
    if (cancelBtn) {
      cancelBtn.addEventListener('click', () => this.hideSprintSelector());
    }

    // Sprint duration buttons
    const durationBtns = document.querySelectorAll('.sprint-duration-btn');
    durationBtns.forEach(btn => {
      btn.addEventListener('click', (e) => {
        const duration = e.target.dataset.duration;
        this.startSprintMode(duration);
      });
    });

    // Results modal buttons
    const playAgainBtn = document.getElementById('playAgainBtn');
    if (playAgainBtn) {
      playAgainBtn.addEventListener('click', () => {
        this.sprint.hideResults();
        const duration = this.sprint.selectedDuration;
        this.sprint.resetSprint();
        this.startSprintMode(duration);
      });
    }

    const closeResultsBtn = document.getElementById('closeResultsBtn');
    if (closeResultsBtn) {
      closeResultsBtn.addEventListener('click', () => {
        this.sprint.hideResults();
        this.sprint.resetSprint();
        this.mode = 'practice';
        this.ui.reset();
      });
    }

    // Player name input for high scores
    const playerNameInput = document.getElementById('playerNameInput');
    if (playerNameInput) {
      playerNameInput.addEventListener('keydown', (e) => {
        if (e.key === 'Enter') {
          e.preventDefault();
          this.sprint.saveHighScore();
        }
      });
    }

    // Clear scores button
    const clearScoresBtn = document.getElementById('clearScoresBtn');
    if (clearScoresBtn) {
      clearScoresBtn.addEventListener('click', () => {
        if (confirm('Are you sure you want to clear all high scores for this duration?')) {
          this.sprint.clearCurrentDurationScores();
        }
      });
    }
  }

  showSprintSelector() {
    // Pause regular game if active
    if (this.session.isSessionActive()) {
      this.session.pause();
    }
    this.sprint.showSprintSelector();
  }

  hideSprintSelector() {
    this.sprint.hideSprintSelector();
    // Resume regular game if was paused
    if (this.session.isPaused()) {
      this.session.resume();
    }
  }

  startSprintMode(duration) {
    // End regular session if active
    if (this.session.isSessionActive()) {
      this.session.end();
    }

    // Hide sprint selector
    this.sprint.hideSprintSelector();

    // Switch to sprint mode
    this.mode = 'sprint';
    this.isWaitingForNext = false;

    // Start sprint
    const question = this.sprint.startSprint(duration);
    this.currentQuestion = question;

    // Display first question
    this.ui.displayQuestion(question);
    this.ui.setGameActive(true);

    // Update UI
    this.ui.startBtn.textContent = 'Sprint Active';
    this.ui.startBtn.disabled = true;
    this.ui.resetBtn.textContent = 'Stop Sprint';
  }

  handleStartNext() {
    if (!this.session.isSessionActive()) {
      this.startGame();
    } else {
      this.nextQuestion();
    }
  }

  startGame() {
    // Start new session
    this.session.start();
    this.ui.setGameActive(true);
    this.isWaitingForNext = false;

    // Load first question
    this.nextQuestion();
  }

  nextQuestion() {
    // Don't clear feedback here - let it persist from previous answer
    // this.ui.clearFeedback();
    this.ui.clearAnswer();

    // Generate new question
    this.currentQuestion = getRandomQuestion();

    // Start timing
    this.session.startQuestion(this.currentQuestion);

    // Display question
    this.ui.displayQuestion(this.currentQuestion);

    // Update statistics
    this.updateStats();

    this.isWaitingForNext = false;
  }

  handleAnswerSubmit() {
    // Ignore if no current question or already waiting for next
    if (!this.currentQuestion || this.isWaitingForNext) {
      return;
    }

    const userAnswer = this.ui.getAnswer();

    // Ignore empty answers
    if (!userAnswer) {
      return;
    }

    let result;

    // Handle based on mode
    if (this.mode === 'sprint') {
      result = this.sprint.submitAnswer(userAnswer);
      if (!result) return;

      // Show feedback
      this.ui.showFeedback(result.isCorrect, result.correctAnswer);

      // Auto-advance to next question
      setTimeout(() => {
        if (this.sprint.isActive()) {
          const nextQ = this.sprint.nextQuestion();
          if (nextQ) {
            this.currentQuestion = nextQ;
            this.ui.displayQuestion(nextQ);
          }
        }
      }, 100);
    } else {
      // Regular practice mode
      result = this.session.submitAnswer(userAnswer);

      // Show feedback
      this.ui.showFeedback(result.isCorrect, result.correctAnswer);

      // Update statistics
      this.updateStats();

      // Set flag to prevent multiple submissions
      this.isWaitingForNext = true;

      // Auto-advance to next question after minimal delay
      setTimeout(() => {
        if (this.session.isSessionActive()) {
          this.nextQuestion();
        }
      }, 100);
    }
  }

  handleReset() {
    // Handle Sprint Mode reset
    if (this.mode === 'sprint') {
      if (confirm('Stop the current sprint?')) {
        this.sprint.stopSprint();
        this.mode = 'practice';
        this.resetGame();
        this.ui.resetBtn.textContent = 'Reset Session';
      }
      return;
    }

    // Regular practice mode reset
    if (this.session.questions.length > 5) {
      const stats = this.session.getStatistics();
      if (!confirm(`Reset session? You've answered ${stats.totalQuestions} questions with ${stats.accuracy}% accuracy.`)) {
        return;
      }
    }

    // Get final stats before reset
    const finalStats = this.session.end();

    // Show summary if there were questions
    if (finalStats.totalQuestions > 0) {
      this.ui.showSessionSummary(finalStats);

      // Reset after delay
      setTimeout(() => {
        this.resetGame();
      }, 3000);
    } else {
      this.resetGame();
    }
  }

  resetGame() {
    // Reset session
    this.session.reset();

    // Reset UI
    this.ui.reset();

    // Clear current question
    this.currentQuestion = null;
    this.isWaitingForNext = false;
  }

  updateStats() {
    const stats = this.session.getStatistics();
    this.ui.updateStatistics(stats);
  }

  // Performance monitoring
  checkPerformance() {
    const trend = this.session.getPerformanceTrend();

    if (trend) {
      if (trend.trend === 'improving' && trend.recentAccuracy > 90) {
        this.ui.showFeedback(true, null);
        this.ui.feedback.textContent = 'ðŸš€ Great progress! You\'re on fire!';
      } else if (trend.trend === 'declining' && trend.recentAccuracy < 50) {
        this.ui.feedback.textContent = 'ðŸ’¡ Take your time, focus on accuracy';
        this.ui.feedback.className = 'feedback';
      }
    }
  }

  // Save/Load functionality (for future enhancement)
  saveProgress() {
    const data = this.session.exportData();
    localStorage.setItem('mathFluencyProgress', JSON.stringify(data));
  }

  loadProgress() {
    const saved = localStorage.getItem('mathFluencyProgress');
    if (saved) {
      try {
        const data = JSON.parse(saved);
        this.session.importData(data);
        this.updateStats();
        this.ui.setGameActive(this.session.isSessionActive());
        return true;
      } catch (e) {
        console.error('Failed to load progress:', e);
      }
    }
    return false;
  }
}

// Initialize game when DOM is ready
function initGame() {
  // Remove any existing demo code
  const demoScript = document.querySelector('script[type="module"]:not([src])');
  if (demoScript) {
    demoScript.remove();
  }

  // Create game instance
  const game = new MathFluencyGame();

  // Expose to window for debugging
  window.game = game;

  // Add developer utilities
  window.clearAllScores = async () => {
    if (confirm('This will clear ALL high scores across all durations. Are you sure?')) {
      const { highScores } = await import('./highScores.js');
      highScores.clearScores(); // Clear all durations
      console.log('All high scores cleared!');
      return 'All scores cleared';
    }
    return 'Cancelled';
  };

  console.log('Developer commands available:');
  console.log('- clearAllScores() : Clear all high scores');
  console.log('- game : Access game instance');

  // Try to load saved progress
  if (game.loadProgress()) {
    console.log('Previous session restored');
  }

  // Save progress periodically
  setInterval(() => {
    if (game.session.isSessionActive()) {
      game.saveProgress();
    }
  }, 30000); // Every 30 seconds

  // Save on page unload
  window.addEventListener('beforeunload', () => {
    if (game.session.isSessionActive()) {
      game.saveProgress();
    }
  });

  console.log('Math Royale initialized! Press Start to begin.');
}

// Start when DOM is loaded
if (document.readyState === 'loading') {
  document.addEventListener('DOMContentLoaded', initGame);
} else {
  initGame();
}

export { MathFluencyGame };