// Sprint Controller Module
// Manages Sprint Mode UI and integration with main game

import { sprintMode } from './sprintMode.js';
import { highScores } from './highScores.js';
import { UIController } from './ui.js';

export class SprintController {
  constructor(uiController) {
    this.ui = uiController || new UIController();
    this.sprintMode = sprintMode;
    this.isSprintActive = false;
    this.selectedDuration = null;
    
    this.initializeElements();
  }

  initializeElements() {
    const doc = (typeof document !== 'undefined') ? document : globalThis.document;
    if (!doc) return;

    // Sprint UI elements
    this.sprintBtn = doc.getElementById('sprintBtn');
    this.sprintSelector = doc.getElementById('sprintSelector');
    this.cancelSprintBtn = doc.getElementById('cancelSprintBtn');
    this.sprintTimerSection = doc.getElementById('sprintTimerSection');
    this.sprintTime = doc.getElementById('sprintTime');
    this.sprintScore = doc.getElementById('sprintScore');
    this.sprintProgressBar = doc.getElementById('sprintProgressBar');
    
    // Sprint duration buttons
    this.durationBtns = doc.querySelectorAll('.sprint-duration-btn');
    
    // Results modal elements
    this.resultsModal = doc.getElementById('sprintResultsModal');
    this.finalScore = doc.getElementById('finalScore');
    this.finalQuestions = doc.getElementById('finalQuestions');
    this.finalAccuracy = doc.getElementById('finalAccuracy');
    this.finalStreak = doc.getElementById('finalStreak');
    this.highScoreSection = doc.getElementById('highScoreSection');
    this.playerNameInput = doc.getElementById('playerNameInput');
    this.leaderboardList = doc.getElementById('leaderboardList');
    this.playAgainBtn = doc.getElementById('playAgainBtn');
    this.closeResultsBtn = doc.getElementById('closeResultsBtn');
  }

  showSprintSelector() {
    if (this.sprintSelector) {
      this.sprintSelector.style.display = 'block';
    }
  }

  hideSprintSelector() {
    if (this.sprintSelector) {
      this.sprintSelector.style.display = 'none';
    }
  }

  startSprint(duration) {
    this.selectedDuration = duration;
    this.isSprintActive = true;
    
    // Configure sprint with callbacks
    this.sprintMode.configure(duration, {
      onUpdate: (data) => this.updateSprintDisplay(data),
      onFinish: (results) => this.handleSprintFinish(results)
    });
    
    // Start the sprint
    this.sprintMode.start();
    
    // Show sprint timer
    if (this.sprintTimerSection) {
      this.sprintTimerSection.style.display = 'flex';
    }
    
    // Hide regular game stats
    const statsSection = document.querySelector('.stats-section');
    if (statsSection) {
      statsSection.style.display = 'none';
    }
    
    // Update button states
    this.updateButtonStates();
    
    // Get first question
    return this.sprintMode.currentQuestion;
  }

  updateSprintDisplay(data) {
    // Update timer
    if (this.sprintTime) {
      this.sprintTime.textContent = this.sprintMode.getFormattedTime();
    }
    
    // Update score
    if (this.sprintScore) {
      this.sprintScore.textContent = data.score;
    }
    
    // Update progress bar
    if (this.sprintProgressBar) {
      const progress = this.sprintMode.getProgress();
      this.sprintProgressBar.style.width = `${progress}%`;
    }
  }

  handleSprintFinish(results) {
    this.isSprintActive = false;
    
    // Hide sprint timer
    if (this.sprintTimerSection) {
      this.sprintTimerSection.style.display = 'none';
    }
    
    // Show regular stats again
    const statsSection = document.querySelector('.stats-section');
    if (statsSection) {
      statsSection.style.display = 'block';
    }
    
    // Show results modal
    this.showResults(results);
  }

  showResults(results) {
    if (!this.resultsModal) return;
    
    // Update result values
    if (this.finalScore) this.finalScore.textContent = results.score;
    if (this.finalQuestions) this.finalQuestions.textContent = results.questionsAnswered;
    if (this.finalAccuracy) this.finalAccuracy.textContent = `${results.accuracy}%`;
    if (this.finalStreak) this.finalStreak.textContent = results.bestStreak;
    
    // Check if high score
    const isHighScore = this.sprintMode.isHighScore();
    if (this.highScoreSection) {
      this.highScoreSection.style.display = isHighScore ? 'block' : 'none';
      if (isHighScore && this.playerNameInput) {
        this.playerNameInput.value = '';
        this.playerNameInput.focus();
      }
    }
    
    // Update leaderboard
    this.updateLeaderboard();
    
    // Show modal
    this.resultsModal.style.display = 'block';
  }

  hideResults() {
    if (this.resultsModal) {
      this.resultsModal.style.display = 'none';
    }
  }

  updateLeaderboard() {
    if (!this.leaderboardList || !this.selectedDuration) return;
    
    const scores = this.sprintMode.getLeaderboard();
    this.leaderboardList.innerHTML = '';
    
    if (scores.length === 0) {
      this.leaderboardList.innerHTML = '<li>No scores yet!</li>';
      return;
    }
    
    scores.slice(0, 10).forEach((score, index) => {
      const li = document.createElement('li');
      li.innerHTML = `
        <span>${index + 1}. ${score.name}</span>
        <span>${score.score} pts</span>
      `;
      this.leaderboardList.appendChild(li);
    });
  }

  saveHighScore() {
    if (!this.playerNameInput || !this.playerNameInput.value.trim()) {
      return false;
    }
    
    const playerName = this.playerNameInput.value.trim();
    const rank = this.sprintMode.saveHighScore(playerName);
    
    if (rank) {
      // Update leaderboard to show new score
      this.updateLeaderboard();
      
      // Hide high score input
      if (this.highScoreSection) {
        this.highScoreSection.style.display = 'none';
      }
      
      // Show success message
      if (rank === 1) {
        this.ui.showFeedback(true, null);
        this.ui.feedback.textContent = 'ðŸ† New #1 High Score!';
      } else {
        this.ui.showFeedback(true, null);
        this.ui.feedback.textContent = `ðŸŽ‰ Ranked #${rank} on the leaderboard!`;
      }
    }
    
    return rank;
  }

  stopSprint() {
    if (this.isSprintActive) {
      this.sprintMode.stop();
      this.isSprintActive = false;
      
      // Hide sprint timer
      if (this.sprintTimerSection) {
        this.sprintTimerSection.style.display = 'none';
      }
      
      // Show regular stats
      const statsSection = document.querySelector('.stats-section');
      if (statsSection) {
        statsSection.style.display = 'block';
      }
      
      // Update button states
      this.updateButtonStates();
    }
  }

  resetSprint() {
    this.sprintMode.reset();
    this.isSprintActive = false;
    this.selectedDuration = null;
    
    // Hide all sprint UI
    this.hideSprintSelector();
    this.hideResults();
    
    if (this.sprintTimerSection) {
      this.sprintTimerSection.style.display = 'none';
    }
    
    // Reset progress bar
    if (this.sprintProgressBar) {
      this.sprintProgressBar.style.width = '0%';
    }
  }

  updateButtonStates() {
    if (this.sprintBtn) {
      this.sprintBtn.disabled = this.isSprintActive;
    }
  }

  isActive() {
    return this.isSprintActive;
  }

  getCurrentQuestion() {
    return this.sprintMode.currentQuestion;
  }

  submitAnswer(userAnswer) {
    return this.sprintMode.submitAnswer(userAnswer);
  }

  nextQuestion() {
    return this.sprintMode.nextQuestion();
  }

  clearCurrentDurationScores() {
    if (this.selectedDuration) {
      highScores.clearScores(this.selectedDuration);
      this.updateLeaderboard();

      // Show feedback
      if (this.ui && this.ui.feedback) {
        this.ui.showFeedback(true, null);
        this.ui.feedback.textContent = 'High scores cleared!';
      }
    }
  }
}